import Vue from 'vue'
import VueRouter from 'vue-router'
import ActivityPage from '../views/Activity.vue'
import CreateAct from '../views/CreateAct.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect:'/activityPage',
  },{
    path:'/activityPage',
    name:ActivityPage,
    component:ActivityPage
  },
  {
    path: '/activitymembermanage/:eventid',
    name: 'ActivityMemberManage',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/ActivityMemberManage.vue')
  },{
    path:'/createact',
    name:CreateAct,
    component:CreateAct

  }
]

const router = new VueRouter({
  routes
})

export default router
