<div class="activity">
    <HeaderBar></HeaderBar>
    <div class="topbar">
        <h4 class="pagetitle">
            活動管理
        </h4>
        <div class="searchwrap">
            <SearchBar @emitkeyword="keywordsearch" placehold="搜尋活動"></SearchBar>
        </div>

        <div class="addactivity">
            <router-link to="/createact">
                <i class="ri-calendar-event-fill"></i>
                <span>新增活動</span>
            </router-link>

        </div>
    </div>
    <div class="content">
        <div class="lefttab">
            <div v-for="(item,index) in itemlist" :key="index">
                <Tabitem :tabname="item.name" :isall=isnotall :isclick="index==tabcurrentindex" @emittabitem=clicktabitem(index,item.name)></Tabitem>
            </div>

        </div>
        <div class="activitylist">

            <div class="activitytopbar">
                <div class="totalnumber">
                    <span>1-15場活動（總共60場）</span>
                </div>
                <div class="dropdownwrap">
                    <Dropdown :catlist="catlist" @itemhandler="activitycata"></Dropdown>
                </div>
                <div class="timefilterwrap">
                    <Timefilter @emitchangetimefilter="timefilter"></Timefilter>
                </div>
            </div>
            <div style="position: relative;" v-for="(act,index) in paginatedata" :key="index">
                <a @click="toactmember(index)">
                    <Activitylist @emitdeleact="deleact(index)" :actname="act[0]" :actpos="act[4]" :acttiime="act[2]" :acttag="act[8]" :actin="act[6]" :actdate="convertdate(act[1])"></Activitylist>
                </a>

            </div>
            <div class="pagination">
                <button @click="uppage" :disabled="pagenumber<=0">
                    <i class="ri-arrow-left-s-line"></i>
                </button>

                <span class="pagenumber">第 {{pagenumber+1}} 頁</span>
                <button :disabled="pagenumber >= pagecount" @click="nextpage">
                    <i class="ri-arrow-right-s-line"></i>
                </button>
            </div>
        </div>

    </div>

    <div class="loadingmask" v-show="loadingcircle">
        <div class="loader"></div>
    </div>
    <Deleactpopup :name="actlist[deleactindex].name" :time="actlist[deleactindex].time" :actin="actlist[deleactindex].actin" v-if="isdele" @emitactdele="deleacthandler"></Deleactpopup>
</div>
</template>

<script>
// @ is an alias to /src
import HeaderBar from '../components/Header'
import SearchBar from '../components/Searchbar'
import Tabitem from '../components/Tabitem'
import Dropdown from '../components/Dropdown'
import Timefilter from '../components/Timefilter'
import Activitylist from '../components/Activitylist'
import Deleactpopup from '../components/Deleactpopup'
import axios from 'axios'

export default {
    data() {
        return {
            catlist: ['全部', '親子', 'DIY', '戶外', '食物'],
            itemlist: [{
                name: '所有場館',
                isclick: true
            }, {
                name: '會館一',
                isclick: false
            }, {
                name: '會館二',
                isclick: false
            }, {
                name: '會館三',
                isclick: false
            }],
            sclist: [],
            fulllist: [],
            isall: true,
            isnotall: false,
            currentcata: '全部',
            currentstadium: '所有場館',
            tabcurrentindex: 0,
            keyword: '',
            isdele: false,
            deleactindex: 0,
            pagenumber: 0,
            pagesize: 5,
            loadingcircle: true,
            timestatus: ''

        }
    },
    mounted() {
        document.body.scrollTop = document.documentElement.scrollTop = 0;
        document.body.style.overflow = 'hidden'
        // axios({
        //         method: 'get',
        //         url: url,
        //     })
        //     .then((response) => {
        //         this.sclist = response.data.res;
        //         this.sclist.splice(0, 1)

        //         for (let i = 0; i < this.sclist.length; i++) {
        //             for (let j = 0; j < this.sclist.length; j++) {
        //                 if (this.sclist[i][0] == this.sclist[j][0] && i != j) {
        //                     this.sclist.splice(j, 1);
        //                     j = 0

        //                 }
        //             }
        //         }
        //         console.log(this.sclist)
        //     })

        axios({
                method: 'get',
                url: 'https://script.google.com/macros/s/AKfycbwu0IumvrBT9W5XybuCu-_hQxn8r8i37EQ-ziiR0N3Mfjkilx2ww_NRS8kEtqNMKV_I/exec',
                params: {
                    mode:'all',
                    id:''
                }
            })
            .then((response) => {
                console.log(response.data)
                this.sclist = response.data
            })

        setInterval(() => {
            this.loadingcircle = false
            document.body.style.overflow = 'visible'
        }, 1600)
    },
    computed: {
        filterlist: {
            get() {
                return this.sclist.filter(item => {
                    if (this.currentcata == "全部" && this.currentstadium == "所有場館" && this.keyword == "" || this.timestatus == "all") {
                        return this.sclist
                    } else {
                        if (this.currentcata == "全部" && this.currentstadium == "所有場館" && item[0].search(this.keyword) != -1) {
                            return item
                        } else if (this.currentcata == "全部") {
                            if (item[4] == this.currentstadium && item[0].search(this.keyword) != -1) {
                                //  console.log('測試',item[0])
                                return item
                            }
                        } else if (this.currentstadium == "所有場館") {
                            if (item[5] == this.currentcata && item[0].search(this.keyword) != -1) {
                                return item
                            }
                        } else if (item[4] == this.currentstadium && item[5] == this.currentcata && item[0].search(this.keyword) != -1) {
                            return item
                        }

                    }

                })
            },
            set(val) {
                this.sclist = val;

            }

        },
        pagecount() {
            let l = this.filterlist.length;
            let s = this.pagesize;
            return Math.floor(l / s)
        },
        paginatedata() {
            const start = this.pagenumber * this.pagesize
            const end = start + this.pagesize;

            return this.filterlist.slice(start, end)
        }
    },
    methods: {
        uppage() {
            this.pagenumber--
        },
        nextpage() {
            this.pagenumber++
        },
        clicktabitem(val, name) {
            this.currentstadium = name;
            this.tabcurrentindex = val;
        },
        activitycata(val) {
            this.currentcata = val

            console.log('有點到', val);
        },
        keywordsearch(val) {
            this.keyword = val;
            console.log(val);
        },
        deleact(index) {
            this.deleactindex = index;
            this.isdele = true;
            // this.actlist.splice(index,1)
            console.log(index)
        },
        deleacthandler(type) {
            console.log(type)
            if (type == "取消") {
                this.isdele = false
            } else if (type == "刪除") {
                this.isdele = false
                this.actlist.splice(this.deleactindex, 1)
            }

        },
        timefilter(val) {
            var today = new Date();
            this.timestatus = val;
            if (val == "closetofar") {
                let newdate = (today.getMonth() + 1) + '/' + today.getDate()
                this.filterlist = this.filterlist.filter((item) => {
                    return this.timecompare(item, newdate, 30)
                })

            } else if (val == "fartoclose") {
                let newdate = (today.getMonth() + 1) + '/' + today.getDate()
                this.filterlist = this.filterlist.filter((item) => {
                    console.log(item)
                    return this.timecompare(item, newdate, 70)
                })
            }
        },
        timecompare(item, currentdatetime, day) {
            let compareitem = item[1].split('/')
            let splitlist = currentdatetime.split('/');
            let num
            let num2
            if (compareitem[1] == "02") {
                num = parseInt(compareitem[1]) * 28 + parseInt(compareitem[2])
            }
            if (compareitem[1] == "04" || compareitem[1] == "06" || compareitem[1] == "09" || compareitem[1] == "11") {
                num = parseInt(compareitem[1]) * 30 + parseInt(compareitem[2])
            }
            if (compareitem[1] == "01" || compareitem[1] == "03" || compareitem[1] == "05" || compareitem[1] == "07" || compareitem[1] == "08" || compareitem[1] == "10" || compareitem[1] == "12") {
                num = parseInt(compareitem[1]) * 31 + parseInt(compareitem[2])
            }

            if (splitlist[0] == "4" || splitlist[0] == "6" || splitlist[0] == "9" || splitlist[0] == "11") {
                num2 = parseInt(splitlist[0]) * 30 + parseInt(splitlist[1])
            }
            if (splitlist[0] == "1" || splitlist[0] == "3" || splitlist[0] == "5" || splitlist[0] == "7" || splitlist[0] == "8" || splitlist[0] == "10" || splitlist[0] == "112") {
                num2 = parseInt(splitlist[0]) * 31 + parseInt(splitlist[1])
            }
            let minusnum = num2 - num;
            if (minusnum < day) {
                console.log('有歐', minusnum)
                return item[1]
            }
        },
        convertdate(date) {
            let str = date
            // console.log(str)
            let strlist = str.split('-')
            let result = (strlist[0] + '/' + strlist[1] + '/' + strlist[2]).substring(0, 10)

            return result
        },
        toactmember(eventid) {
            this.$store.state.currentlistinfo = this.sclist[eventid]
            window.sessionStorage.setItem('storeeventinfo', JSON.stringify(this.sclist[eventid]))
            this.$router.push({
                name: 'ActivityMemberManage',
                params: {
                    eventid: this.sclist[eventid][14]
                }
            })
        }
    },
    components: {
        HeaderBar,
        SearchBar,
        Tabitem,
        Dropdown,
        Timefilter,
        Activitylist,
        Deleactpopup
    }
}
</script>

