<template>
<div class="timefilter" @click="changetimefilter">
    <span v-if="status=='all'">開始時間</span>
    <span v-else-if="status=='closetofar'">開始時間(近30天)</span>
    <span v-else-if="status=='fartoclose'">開始時間(近60天)</span>
    <!-- <div class="adjust">
        <i class="ri-arrow-drop-up-fill" :class="{clicked:status=='fartoclose'}"></i>
        <i class="ri-arrow-drop-down-fill" :class="{clicked:status=='closetofar'}"></i>
    </div> -->

</div>
</template>

<script>
export default {
    data() {
        return {
            status:'all',
            currentindex:0
        }
    },
    methods:{
        changetimefilter(){
            this.currentindex++;
            this.currentindex = this.currentindex%3;
            if(this.currentindex == 1){
                this.status = 'closetofar'
                this.$emit('emitchangetimefilter',this.status)
            }else if(this.currentindex == 2){
                this.status = 'fartoclose'
                 this.$emit('emitchangetimefilter',this.status)
            }else{
                this.status = 'all'
                 this.$emit('emitchangetimefilter',this.status)
            }

        }
    }
}
</script>

